export interface OJobsMgrInit extends OSkinableInit {
	reg: IReg<ICoreUniverseEnv & IExecutorInUniverse & IUiEnv>,
	scope?: 'my' | 'all',
	/** Liste de promises de UiJobFactory, ou de fctions retournant cette promise. Liste issue de executor:jobs:factories à défaut */
	jobsFactoriesList?: [],
	/** Init de=u tab */
	jobsTab?: OTabsInit<any>,
	/** Init des grids */
	jobsGrids?: OJobsGridInit<any>,
	/** Tab à afficher */
	showTab?: EJobTabStatus
}

enum EJobTabStatus {
	alive = 'alive',
	planned = 'planned',
	failed = 'failed',
	done = 'done',
}


export class JobsMgr<E extends IRegPointer<IExecutorInUniversePointer & IUiEnv>, D extends IJobBuilderActionContext> extends BaseAreaViewAsync<E> implements IGridLineDrawer {
	reg: IReg<IExecutorInUniversePointer & IUiEnv>;

	params: OJobsMgrInit;

	uiJobsFactories: UiJobFactory<E, D>[];

	protected _headband: MsgLabel;

	protected _tabs: JobsTabs;

	protected _toolbar: BarShared<JobsMgr<any, any>>;

	protected _fetching: "error" | "pending" | null;

	/** Mise en pause de l'éxécution des jobs */
	get frozenStatus(): boolean {return this.hasAttribute("data-frozen")};

	set frozenStatus(val: boolean) {
		DOM.setAttrBool(this, "data-frozen", val);
	}

	protected async _initialize(init: OJobsMgrInit): Promise<void> {
		if (!this.reg) this.reg = REG.createSubReg(this.findReg(init));
		this.params = Object.assign({
			jobsFactoriesList: [EXECUTOR.JOBS_FACTORIES_LIST],
		}, init);

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);

		const uiJobsFactoriesFcts: (() => Promise<UiJobFactory<any, any>>)[] = this.reg.mergeLists(...this.params.jobsFactoriesList) as any;
		const uiJobsFactoriesPromises = uiJobsFactoriesFcts ? uiJobsFactoriesFcts.map((entry) => typeof entry === "function" ? entry.call(this) : entry) : null;
		this.uiJobsFactories = await Promise.all(uiJobsFactoriesPromises || []) as UiJobFactory<any, any>[];

		// Actions
		this.reg.addToList("actions:jobsmgr:toolbar", "actionFreezeExecutor", 1, new FreezeExecutorBtn());
		this.reg.addToList("actions:jobsmgr:toolbar", "actionRefreshUi", 1, actionRefreshUi);
		this.reg.addToList("actions:jobsmgr:toolbar", "actionNewJob", 1, ActionNewJobs.SINGLETON);

		this.reg.addToList("actions:jobsmgr", "actionDuplicateJob", 1, new ActionWrapperRefresMgr(this).setOverridenSvc(new ActionWrapperCreateJob(this).setOverridenSvc(new ActionDuplicateJob(this))));
		this.reg.addToList("actions:jobsmgr", "actionDeleteJobs", 1, new ActionWrapperRefresMgr(this).setOverridenSvc(ActionDeleteJobs.SINGLETON));
		//this.reg.addToList("actions:jobsmgr", "actionScheduleJobs", 1, new ActionWrapperRefresMgr(this).setOverridenSvc(ActionScheduleJobs.SINGLETON));
		this.reg.addToList("actions:jobsmgr", "actionForceExecuteJobs", 1, new ActionWrapperRefresMgr(this).setOverridenSvc(ActionForceExecuteJobs.SINGLETON));
		this.reg.addToList("actions:jobsmgr", "actionMoveInQueueJobs", 1, new ActionWrapperRefresMgr(this).setOverridenSvc(ActionMoveInQueueJobs.SINGLETON));

		this._headband = sr.appendChild(<MsgLabel class="headband" î={{
			label: "Exécution des traitements gelée",
			icon: "/@skin@/core/actions/jobs/frozen.svg",
			level: "warning",
		} as OMsgLabelInit}/>) as MsgLabel;

		let jbBoxElt = sr.appendChild(<div class="jobsBox"/>) as HTMLDivElement;

		// Colonnes
		const col_id = new GridColDef('id')
			.setLabel("Id.")
			.setDescription("Identifiant")
			.setSortable(false)
			.setCellBuilder(new CellBuilderString("id"));
		const col_name = new GridColDef('name')
			.setLabel("Label")
			.setFlex("6rem", 1, 1)
			.setCellBuilder(new CellBuilderJobLabel(this));
		const col_lastQueued = new GridColDef('lastQueued')
			.setLabel("Exécution")
			.setDescription("Date d'exécution")
			.setFlex("8rem", 1, 1)
			.setCellBuilder(new CellBuilderDate("lastQueued").setCellClass("center").setOptions({year: "numeric", month: "2-digit", day: "2-digit", hour: "2-digit", minute: "2-digit", second: "2-digit"}));
		const col_planned = new GridColDef('planned')
			.setLabel("Planification")
			.setDescription("Date planifiée")
			.setFlex("8rem", 1, 1)
			.setCellBuilder(new CellBuilderDate("planned").setCellClass("center").setOptions({year: "numeric", month: "2-digit", day: "2-digit", hour: "2-digit", minute: "2-digit", second: "2-digit"}));
		const col_createdBy = new GridColDef('createdBy')
			.setLabel("Créateur")
			.setDescription("Compte ayant créé ce traitement")
			.setFlex("3rem", 1, 1)
			.setCellBuilder(new CellBuilderAccount(this.reg, "createdBy", true));
		const col_status = new GridColDef('lastStatus')
			.setLabel("Statut")
			.setFlex('2rem', 1, 1)
			.setCellBuilder(new CellBuilderEnum('lastStatus', new Map([
				[EJobStatus.waiting, "En attente"],
				[EJobStatus.launched, "Chargé"],
				[EJobStatus.pending, "En cours d'exécution"],
				[EJobStatus.done, "Terminé"],
				[EJobStatus.failed, "En échec"],
				[EJobStatus.planned, "Planifié"],
			])).setCellClass("center"));

		/** Tabs */
		let areas: IArea[] = [];
		areas.push(new AreaAsync(EJobTabStatus.alive)
			.setLabel("En cours").setDescription("Traitements en cours")
			.setBodyBuilder((ctx) => {
				return <JobsGridView id={EJobTabStatus.alive} î={Object.assign({
					columnDefs: [col_id, col_name, col_lastQueued, col_createdBy, col_status],
					reg: this.reg,
					emptyBody: this.emptyBody,
					skinOver: 'jobs-mgr/grid',
					actions: this.reg.mergeLists("actions:jobsmgr", "actions:jobsmgr:alive"),
					accelKeyMgr: new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:jobsmgr", "accelkeys:jobsmgr:alive")),
					selectAction: new DisplayJobDetails(this),
					lineDrawer: this,
				} as OJobsGridInit<any>, init.jobsGrids)}/>
			}));
		if (this.reg.env.universe.executor.hasFeature("scheduling")) areas.push(new AreaAsync(EJobTabStatus.planned)
			.setLabel("Planifiés").setDescription("Traitements planifiés")
			.setBodyBuilder((ctx) => {
				return <JobsGridView id={EJobTabStatus.planned} î={Object.assign({
					columnDefs: [col_id, col_name, col_planned, col_createdBy],
					reg: this.reg,
					emptyBody: this.emptyBody,
					skinOver: 'jobs-mgr/grid',
					actions: this.reg.mergeLists("actions:jobsmgr", "actions:jobsmgr:planned"),
					accelKeyMgr: new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:jobsmgr", "accelkeys:jobsmgr:planned")),
					selectAction: new DisplayJobDetails(this),
					lineDrawer: this,
				} as OJobsGridInit<any>, init.jobsGrids)}/>
			}));
		areas.push(new AreaAsync(EJobTabStatus.failed)
			.setLabel("En échec").setDescription("Traitements en échec")
			.setBodyBuilder((ctx) => {
				return <JobsGridView id={EJobTabStatus.failed} î={Object.assign({
					columnDefs: [col_id, col_name, col_lastQueued, col_createdBy],
					reg: this.reg,
					emptyBody: this.emptyBody,
					skinOver: 'jobs-mgr/grid',
					actions: this.reg.mergeLists("actions:jobsmgr", "actions:jobsmgr:failed"),
					accelKeyMgr: new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:jobsmgr", "accelkeys:jobsmgr:failed")),
					selectAction: new DisplayJobDetails(this),
					lineDrawer: this,
				} as OJobsGridInit<any>, init.jobsGrids)}/>
			}));
		areas.push(new AreaAsync(EJobTabStatus.done)
			.setLabel("Terminés").setDescription("Traitements terminés")
			.setBodyBuilder((ctx) => {
				return <JobsGridView id={EJobTabStatus.done} î={Object.assign({
					columnDefs: [col_id, col_name, col_lastQueued, col_createdBy],
					reg: this.reg,
					emptyBody: this.emptyBody,
					skinOver: 'jobs-mgr/grid',
					actions: this.reg.mergeLists("actions:jobsmgr", "actions:jobsmgr:done"),
					accelKeyMgr: new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:jobsmgr", "accelkeys:jobsmgr:done")),
					selectAction: new DisplayJobDetails(this),
					lineDrawer: this,
				} as OJobsGridInit<any>, init.jobsGrids)}/>
			}));
		this._tabs = jbBoxElt.appendChild(<JobsTabs î={Object.assign({
			vertical: true,
			areas: areas,
		} as OTabsInit<any>, init.jobsTab)} id="tabs"/>) as JobsTabs;

		/** Toolbar */
		this._toolbar = jbBoxElt.appendChild(new BarShared<this>().initialize({
			reg: this.reg,
			startActions: ACTION.injectSepByGroup(
				this.reg.mergeLists("actions:jobsmgr:toolbar", "actions:jobsmgr:toolbar:" + this.reg.env.universe.getId()),
				this.reg.getPref("actions.jobsmgr.groupOrder", "refresh add *"), this) as Action<this>[],
			actionContext: this,
			focusListening: this._tabs,
		} as OBarSharedInit<this>));
		DOM.setAttrBool(this._toolbar, "vertical", true);

		this.addEventListener("keydown", (ev) => {
			if (ev.key === 'Escape' && this.currentJobsGrig) {
				this.currentJobsGrig.selectByJob([]);
				ev.stopPropagation()
			}
		});
	}

	onViewShown() {
		if (this.params.showTab)
			this._tabs.selectTab(this._tabs.getTabByCode(this.params.showTab));
		this.fetchAll();
	}

	get currentJobsGrig(): JobsGridView {
		return this._tabs.selectedTab ? this._tabs.selectedTab.view as JobsGridView : null;
	}

	/** Rafraichissement global des données affichées */
	async fetchAll(excludeCurrent: boolean = true) {
		await this.fetchTabJobs();
		await this.fetchStats();
	}

	/** Rafraichit les compteurs des tabs et l'état d'exécution
	 * @param stats : stats computés en amont. Si spécifé, refreh ui uniquement
	 */
	async fetchStats(stats?: JJobsStats) {
		if (this._fetchingCountersPending) return;
		let currentTab = this._tabs.selectedTab;
		if (!currentTab) return;
		let currentTabStatus = currentTab.area.getId() as EJobTabStatus;
		try {
			this._fetchingCountersPending = true;
			if (!stats)
				stats = await EXECUTOR.getStats(this.reg.env.universe.executor);
			this._tabs.refreshCounterValue(EJobTabStatus.alive, stats.countInQueue.toString());
			this._tabs.refreshCounterValue(EJobTabStatus.failed, stats.countFailed.toString());
			this.frozenStatus = stats.frozen;
		} catch (e) {
			this._tabs.refreshCounterValue(EJobTabStatus.alive, '-');
			this._tabs.refreshCounterValue(EJobTabStatus.failed, '-');
			this.frozenStatus = null;
			await ERROR.log("Count jobs failed", e);
		} finally {
			this._fetchingCountersPending = false;
		}
		this._toolbar.refresh();
	}

	private _fetchingCountersPending = false;

	/** Rafraichit la tab courante affichée */
	protected async fetchTabJobs() {
		if (this._fetchingTabJobsPending) return;
		let currentTab = this._tabs.selectedTab;
		if (!currentTab) return;
		let currentTabStatus = currentTab.area.getId() as EJobTabStatus;
		this._fetching = "pending";
		try {
			this._fetchingTabJobsPending = true;
			let jobCriterions: JJobCriterion = this.computeFetchCriterion(currentTabStatus);
			const result = this.params.scope == "my" ?
				await EXECUTOR.findMyJobs(this.reg.env.universe.executor, jobCriterions) :
				await EXECUTOR.findJobs(this.reg.env.universe.executor, jobCriterions);

			this._fetching = null;
			const currentSelect = this.currentJobsGrig.getSelectedJobs();
			this.currentJobsGrig.jobsGridDatas.setDatas(result.jobs);
			this.currentJobsGrig.selectByJob(currentSelect);
		} catch (e) {
			this._fetching = "error";
			this.currentJobsGrig.jobsGridDatas.setDatas([]);
			await ERROR.log("List jobs failed", e);
		} finally {
			this._fetchingTabJobsPending = false;
		}
	}

	private _fetchingTabJobsPending = false;

	/** */
	private computeFetchCriterion(jobStatus: EJobTabStatus): JJobCriterion {
		// TODO : critère de filtre apporté par le init
		let jobCriterions: JJobCriterion = {};
		if (jobStatus == EJobTabStatus.alive) {
			jobCriterions.statuses = [EJobStatus.waiting, EJobStatus.launched, EJobStatus.pending];
			jobCriterions.inQueue = true;
		} else if (jobStatus == EJobTabStatus.done)
			jobCriterions.statuses = [EJobStatus.done]
		else if (jobStatus == EJobTabStatus.failed)
			jobCriterions.statuses = [EJobStatus.failed]
		else if (jobStatus == EJobTabStatus.planned)
			jobCriterions.statuses = [EJobStatus.planned]
		return jobCriterions;
	}

	/**
	 * Tree
	 */
	redrawLine(row: GridDataRowJson<JJobBase>, line: HTMLElement): void {
		DOM.setAttr(line, "data-status", row.rowDatas.lastStatus);
	}

	emptyBody(): Element {
		switch (this._fetching) {
		case "pending":
			return <c-msg label="Chargement en cours..." level="loading"/>
		case "error":
			return <c-msg label="Chargement en erreur" level="error"/>
		default:
			return <c-msg label="Aucun traitement"/>
		}
	}

	/**
	 * IViewApi
	 */
	onViewBeforeHide(close?: boolean): boolean {
		return false;
	}

	async onViewWaitForHide(close?: boolean): Promise<boolean> {
		return true;
		/*return !await this.computeDirtyStatus() || await POPUP.confirm(`Des propriétés n'ont pas été enregistrées. Voulez-vous abandonner ces modifications et quitter ?`, this.reg.env.uiRoot, {
			okLbl: "Abandonner les modifications et quitter",
			cancelLbl: "Reprendre l'édition"
		});*/
	}

}

