/** JobsGridView */
class JobsGridView extends JobsGrid implements IView {

	protected _detailsElt: HTMLDivElement;

	protected _resizerElt: Resizer;

	protected async _initialize(init: OJobsGridInit<IJobGridCtx>): Promise<void> {
		super._initialize(init);
		DOM.setAttrBool(this.shadowRoot.lastElementChild, "c-resizable", true);
		this._resizerElt = this.shadowRoot.appendChild(<c-resizer c-orient="column" class="detailsRsz"/>) as Resizer;
		this._detailsElt = this.shadowRoot.appendChild(<div class="details" c-resizable/>) as HTMLDivElement;
		await this.showJobDetail(null);
	}

	/**
	 * Details
	 */
	async showJobDetail(jobs: JJobBase[]): Promise<void> {
		const me = DOMSH.findHost<JobsMgr<any, any>>(this);
		if (jobs && jobs.length == 1) {
			let jobBase = jobs[0];
			this._currentJobDetailId = jobBase.id; //raceCond
			let job = await EXECUTOR.getJob(me.reg.env.universe.executor, jobBase.id);
			this._detailsElt.innerText = "";
			if (this._currentJobDetailId !== job.id) return; //raceCond
			let factory = me.uiJobsFactories.find((factory) => factory.matchJob(jobBase));
			if (factory) {
				await factory.buildJobDetails(this._detailsElt, me, job);
				if (this._currentJobDetailId !== job.id) return; //raceCond
				DOM.setHidden(this._detailsElt, false);
				DOM.setHidden(this._resizerElt, false);
				this.ensureRowVisibleByJobId(job.id);
				this._currentJobDetailId = undefined;
			} else {
				DOM.setHidden(this._detailsElt, false);
				DOM.setHidden(this._resizerElt, false);
				this._detailsElt.appendChild(UiJobFactory.buildJobTitleDetails(job));
				this._detailsElt.appendChild(UiJobFactory.buildJobPropsDetails(job));
				this._detailsElt.appendChild(UiJobFactory.buildJobTriesDetails(job, me.reg));
			}
		} else {
			DOM.setHidden(this._detailsElt, true);
			DOM.setHidden(this._resizerElt, true);
		}
	}

	private _currentJobDetailId: string;

	onViewShown(): void {
		const me = DOMSH.findHost<JobsMgr<any, any>>(this);
		if (me) me.fetchAll();
	}
}

