/** JobsTabs */
class JobsTabs extends Tabs {
	private _countersElts: Map<EJobTabStatus, HTMLSpanElement> = new Map();

	initialize(init?: OTabsInit<any>): this {
		super.initialize(init);
		this.findReg(init).installSkin("c-tabs", this.shadowRoot);
		return this;
	}

	_initTab(tab: Tab<any>) {
		super._initTab(tab);
		tab._createLabel();
		REG.findReg(this).installSkin("jobs-mgr-jobstabs-tab", tab.shadowRoot);
		if ([EJobTabStatus.alive, EJobTabStatus.failed].includes(tab.area.getId() as EJobTabStatus))
			this._countersElts.set(tab.area.getId() as EJobTabStatus, tab.shadowRoot.appendChild(<span class={"count " + tab.area.getId()}>-</span>));
	}

	refreshCounterValue(jobTabStatus: EJobTabStatus, value: string) {
		if (this._countersElts.has(jobTabStatus))
			this._countersElts.get(jobTabStatus).textContent = value;
	}

}

