/** Colonnes */
export class CellBuilderJobLabel extends CellBuilderIconLabel {

	constructor(public jobsMgr: JobsMgr<any, any>) {
		super(null);
	}

	private getJobUiFactory(row: GridDataRowJsonTree<JJob>): UiJobFactory<any, any> {
		if (!("_jobFactory" in row.cacheHolder) && this.jobsMgr.uiJobsFactories)
			row.cacheHolder['_jobFactory'] = this.jobsMgr.uiJobsFactories.find((factory) => factory.matchJob(row.rowDatas as JJob));
		return row.cacheHolder['_jobFactory'];
	}

	_getValue(row: GridDataRowJsonTree<JJob>) {
		if ("_jobLabel" in row.cacheHolder)
			return row.cacheHolder['_jobLabel'];
		return row.cacheHolder['_jobLabel'] = this.getJobUiFactory(row) ? this.getJobUiFactory(row).getLabel(this.jobsMgr) : row.rowDatas.jobSgn
	}

	_getIcon(row: GridDataRowJsonTree<JJob>) {
		if ("_jobIcon" in row.cacheHolder)
			return row.cacheHolder['_jobIcon'];
		return row.cacheHolder['_jobIcon'] = this.getJobUiFactory(row) ? this.getJobUiFactory(row).getIcon(this.jobsMgr) : null
	}
}

